/**
 * Loads an item from localStorage based on the provided key.
 * @param {string} key - The key to retrieve the item.
 * @returns {any} - The retrieved item or undefined if not found.
 */
const loadItem = (key) => {
  try {
    const serializedState = localStorage.getItem(key);
    return serializedState === null ? undefined : JSON.parse(serializedState);
  } catch (err) {
    return undefined;
  }
};

/**
 * Saves an item to localStorage with a specified key.
 * @param {string} key - The key to set for the item.
 * @param {any} value - The value to be stored.
 */
const saveItem = (key, value) => {
  try {
    const serializedState = JSON.stringify(value);
    localStorage.setItem(key, serializedState);
  } catch (err) {
    // Ignoring write error as of now
  }
};

/**
 * Clears an item from localStorage based on the provided key.
 * @param {string} key - The key of the item to be removed.
 * @returns {boolean} - Returns true if the item was successfully removed, otherwise false.
 */
const clearItem = (key) => {
  try {
    localStorage.removeItem(key);
    return true;
  } catch (err) {
    return false;
  }
};

export default {
  loadItem,
  saveItem,
  clearItem,
};
