/**
 * This function validates the endpointObj and returns the appropriate object based on the current environment.
 *
 * @param {Object} endpointObj - The endpoint object that contains 'absoluteUrls' and 'partialUrls' keys.
 * @returns {Object} - The appropriate object based on the current environment.
 * @throws {Error} - Throws an error if endpointObj does not only have 'absoluteUrls' and 'partialUrls' keys.
 * @throws {Error} - Throws an error if 'absoluteUrls' and 'partialUrls' are not objects.
 * @throws {Error} - Throws an error if 'absoluteUrls' and 'partialUrls' do not have the same keys.
 *
 * @example
 * const endpointObj = {
 *   absoluteUrls: {
 *     API_URL_AUTH: 'http://localhost:3000/auth',
 *     API_URL_USERS: 'http://localhost:3000/users',
 *   },
 *   partialUrls: {
 *     API_URL_AUTH: '/auth',
 *     API_URL_USERS: '/users',
 *   },
 * };
 * const currentAPIs = endpointUtil(endpointObj);
 */

function endpointUtil(endpointObj, appName, appType) {
  // Check if endpointObj has only 'absoluteUrls' and 'partialUrls' keys
  const keys = Object.keys(endpointObj);
  if (
    keys.length !== 2 ||
    !keys.includes("absoluteUrls") ||
    !keys.includes("partialUrls")
  ) {
    throw new Error(
      "endpointObj should only have 'absoluteUrls' and 'partialUrls' keys."
    );
  }

  // Check if 'absoluteUrls' and 'partialUrls' are objects
  if (
    typeof endpointObj.absoluteUrls !== "object" ||
    typeof endpointObj.partialUrls !== "object"
  ) {
    throw new Error("'absoluteUrls' and 'partialUrls' should be objects.");
  }

  // Check if 'absoluteUrls' and 'partialUrls' have the same keys
  const absoluteUrlsKeys = Object.keys(endpointObj.absoluteUrls);
  const partialUrlsKeys = Object.keys(endpointObj.partialUrls);
  if (
    absoluteUrlsKeys.length !== partialUrlsKeys.length ||
    !absoluteUrlsKeys.every((key) => partialUrlsKeys.includes(key))
  ) {
    throw new Error(
      "The number of keys in 'absoluteUrls' and 'partialUrls' should be the same and a key present in 'absoluteUrls' should also be present in 'partialUrls'."
    );
  }

  //This is for test environment, return config same as absoluteUrls environment.
  if (process.env.NODE_ENV === "test") {
    return endpointObj.absoluteUrls;
  }

  const endpoints = new Proxy(
    {},
    {
      get: function (target, property) {
        const isPartialpathAllowed =
          appType !== 'external' &&
          localStorage.getItem(`${appName}__isStandalone`) !== 'true' &&
          process.env.NODE_ENV === 'production';
        return endpointObj[isPartialpathAllowed ? 'partialUrls' : 'absoluteUrls'][property];
      },
    },
  );

  // Return the object based on process.env.NODE_ENV
  return endpoints;
}

export default endpointUtil;
