/**
 * Vulcan SDK Utility Package singleton class.
 */
class UtilPackage {
    #initialized = false;
    #initializedData = null;
  
    /**
     * Initializes the Utility Package.
     * @param {Object} data - The data to initialize the package with.
     * @throws {Error} Throws an error if the package is already initialized.
     */
    init(data) {
      if (!this.#initialized) {
        this.#initializedData = data;
        this.#initialized = true;
        console.log('Vulcan SDK Initialization done.');
      } else {
        throw new Error('Vulcan SDK Utility package already initialized.');
      }
    }
  
    /**
     * Gets the initialized data from the Utility Package.
     * @returns {Object} The initialized data.
     * @throws {Error} Throws an error if the package is not yet initialized.
     */
    getData() {
      if (!this.#initialized) {
        throw new Error('Vulcan SDK Utility package not yet initialized.');
      }
      return this.#initializedData;
    }
  }
  
  const utilPackageInstance = new UtilPackage();
  export default utilPackageInstance;
  