import isEmpty from "lodash/isEmpty";
import decompressUtil from './decompressUtil';
import getVulcanSDKData from './getVulcanSDKData';
import {
  REQ_HEADERS_X_CAP_UI_TYPE,
  REQ_HEADERS_X_CAP_UI_TYPE_VALUE,
  REQ_HEADERS_X_CAP_VULCAN_APP_NAME,
  REQ_HEADERS_X_CAP_COMPRESS_RESPONSE,
} from "./constants";

/**
 * Initializes an api caller function with specified options.
 * @param {Object} initOptions - Initialization options for the HTTP request.
 * @param {Function} [initOptions.redirectIfUnauthenticated] - Function to redirect if unauthenticated.
 * @param {boolean} [initOptions.skipRedirectIfUnauthenticated=false] - Skip redirection if unauthenticated.
 * @param {boolean} [initOptions.skipTimestampQuery=false] - Skip sending current timestamp in request url query params.
 * @param {boolean} [initOptions.hideAllErrors=false] - Hide all errors.
 * @param {number} [initOptions.timeout] - This will be global timeout for requests. Individual request timeout will have higher priority.
 * @param {boolean} [initOptions.sendVulcanMetricHeaders=false] - Send Vulcan metric headers.
 * @param {boolean} [initOptions.skipParsingJson=false] - Skip parsing JSON response.
 * @param {boolean} [initOptions.useResponseCompression=false] - Use compression for API calls.
 * @returns {Function} - Function to make HTTP requests.
 */
function initializeApiCaller(initOptions) {
  if (!typeof initOptions || typeof initOptions !== "object") {
    throw new Error(
      "You have not specified correct initOptions to initialize api caller"
    );
  }

  if (
    initOptions.skipRedirectIfUnauthenticated &&
    (!initOptions.redirectIfUnauthenticated ||
      typeof initOptions.redirectIfUnauthenticated !== "function")
  ) {
    throw new Error(
      "You have not specified correct redirectIfUnauthenticated function to initialize api caller"
    );
  }

  /**
   * Parses Text response.
   * @param {Response} response - HTTP response object.
   * @returns {Promise<any>} - Promise resolving to parsed Text.
   */
  function parseText(response) {
    return response.text();
  }
  /**
   * Parses JSON response.
   * @param {Response} response - HTTP response object.
   * @returns {Promise<any>} - Promise resolving to parsed JSON.
   */
  function parseJSON(response) {
    return response.json();
  }
  /**
   * Shows an error message.
   * @param {string} errorMessage - Error message to display.
   * @param {Object} singleErrorOption - Single error options.
   * @param {boolean} [singleErrorOption.hideError=false] - Boolean to hide the error.
   * @param {string} [singleErrorOption.message=""] - Specific error message for an individual request.
   */
  function showError(errorMessage, singleErrorOption) {
    if (initOptions.hideAllErrors || singleErrorOption.hideError) return;
  
    import("@capillarytech/cap-ui-library/CapNotification").then(({ default: CapNotification }) => {
      CapNotification.error({
        message:
          singleErrorOption.message ||
          errorMessage.message ||
          "Api error occurred",
      });
    });
  }
  /**
   * Fetch with a specified timeout.
   * @param {number} ms - Timeout in milliseconds.
   * @param {Promise<any>} promise - Promise to be executed.
   * @returns {Promise<any>} - Promise with timeout handling.
   */
  function fetchWithTimeout(ms, promise) {
    //https://github.com/github/fetch/issues/175#issuecomment-125779262
    return new Promise(function (resolve, reject) {
      setTimeout(function () {
        reject({
          message: "Request timeout",
          errorLocation: window.location.href,
        });
      }, ms);
      promise.then(resolve, reject);
    });
  }

  function checkStatusCode(res) {
    if (res && res.code) {
      return /^[4-5][0-9][0-9]$/.test(res.code);
    }
    return false;
  }

  function checkStatus(response) {
    if (
      (response.status >= 200 && response.status < 300) ||
      response.status === 500 ||
      response.status === 400 ||
      response.status === 403 ||
      response.status === 406 ||
      response.status === 412 ||
      response.status === 422 ||
      response.status === 409
    ) {
      return response;
    }

    if (!initOptions.skipRedirectIfUnauthenticated) {
      initOptions.redirectIfUnauthenticated(response);
    }

    const error = new Error(response.statusText);
    error.response = response;
    throw error;
  }
  /**
   * Makes an HTTP request.
   * @param {string} url - URL for the request.
   * @param {object} options - Request options.
   * @param {number} [timeout] - Request timeout in milliseconds. Specific request timeout -> init options timeout -> default timeout of 1 min
   * @param {Object} singleErrorOption - Single error options.
   * @param {boolean} [singleErrorOption.hideError=false] - Boolean to hide the error.
   * @param {string} [singleErrorOption.message=""] - Specific error message for an individual request.
   * @returns {Promise<any>} - Promise resolving to the response or error.
   */
  function request(url, options, timeout, singleErrorOption = {}) {
    const fetchUrl =
      (initOptions?.skipTimestampQuery || options?.skipTimestampQuery)
        ? url 
        : url.indexOf("?") !== -1
          ? `${url}&time=${Date.now()}`
          : `${url}?time=${Date.now()}`;
    try {
      const sendVulcanMetricHeaders = initOptions?.sendVulcanMetricHeaders || options?.sendVulcanMetricHeaders || false;
      const useResponseCompression = initOptions?.useResponseCompression || options?.useResponseCompression || false;
      const skipParsingJson = initOptions?.skipParsingJson || options?.skipParsingJson || false;
      const { appName = '' } = getVulcanSDKData() || {};
      // Appending this headers to track all the request that are originated from Vulcan.
      if (sendVulcanMetricHeaders) {
        options.headers.append(REQ_HEADERS_X_CAP_VULCAN_APP_NAME, appName);
        options.headers.append(REQ_HEADERS_X_CAP_UI_TYPE, REQ_HEADERS_X_CAP_UI_TYPE_VALUE);      
      }
      if (useResponseCompression) {
        options.headers.append(REQ_HEADERS_X_CAP_COMPRESS_RESPONSE, 'true');
      }
      return fetchWithTimeout(
        timeout || initOptions.timeout || 60000,
        fetch(fetchUrl, options)
      )
        .then(checkStatus)
        .then((response) => (skipParsingJson || useResponseCompression) ? parseText(response) : parseJSON(response))
        .then((res) => {
          if (useResponseCompression) {
            res = decompressUtil(res);
          }
          if (res.message === 'Access Forbidden') {
            return {
              status: 403,
              message: {
                name: "GENERIC_ACCESS_DENIED",
                message: "Access Denied! Please contact Capillary support.",
                errorCode: "-800403"
              },
              success: false,
            };
          }
          if (!isEmpty(res.errors) || checkStatusCode(res)) {
            showError(res, singleErrorOption);
          }
          return res;
        })
        .catch((error) => {
          if (error) {
            showError(error, singleErrorOption);
          }
          return error;
        });
    } catch (err) {
      if (err) {
        showError(err, singleErrorOption);
      }
      return err;
      // eslint-disable-next-line
    }
  }
  return request;
}

export default {
  initializeApiCaller,
};
